import React from "react";
import { projects } from "../constants";
import Typography from "./Typography";





export default function Projects () {
    return (
        projects.map(project =>
            <div style={styles.projects}>
                <Typography
                    text={project.projectName}
                    textAlign="left"
                    fontWeight="400"
                    fontSize="18px"
                    margin="0 0 10px 0"
                />
                <Typography
                    text={project.summary}
                    textAlign="left"
                    fontWeight="200"
                    fontSize="16px"
                    margin="0 0 10px 0"
                />
                <Typography
                    text={"Technology Used: " +  project.technologyStack}
                    textAlign="left"
                    fontWeight="200"
                    fontSize="16px"
                    margin="0 0 20px 0"
                />

                {project.github ?
                    <p style={styles.links}>Github: <a href={project.github}>{project.github}</a> </p> :
                    <div />
                }
                {
                    project.link ?
                    <p style={styles.links}>Link: <a href={project.link}>{project.link}</a> </p> :
                    <div />
                }
                <div style={{marginBottom: "40px"}} />
            </div>
        )
    )
}


const styles = {
    projects: {
        display: "flex",
        flexDirection: "column",
    },
    links: {
        margin: 0,
        padding: 0,
    }
}
