import React from "react";

import { workExperience } from "../constants";
import Typography from "./Typography";

export default function Experience() {
  return workExperience.map((experience) => (
    <div style={styles.experience} key={experience.id}>
      <Typography
        text={experience.companyName}
        textAlign="left"
        fontWeight="400"
        fontSize="18px"
      />
      <Typography
        text={experience.title}
        textAlign="left"
        fontWeight="200"
        fontSize="12px"
        margin="0 0 10px 0"
      />
      <Typography
        text={experience.summary}
        textAlign="left"
        fontWeight="200"
        fontSize="16px"
        margin="0 0 10px 0"
      />

      <Typography
        text={"Technology Used: " + experience.technologyUsed}
        textAlign="left"
        fontWeight="200"
        fontSize="16px"
        margin="0 0 40px 0"
      />
    </div>
  ));
}

const styles = {
  experience: {
    display: "flex",
    flexDirection: "column",
  },
};
